@import "../../App.scss";

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 1px 2px 6px rgb(49, 49, 49);
  width: 100%;
  height: 78px;
  padding: 0 80px;
  background-color: $colour-background;

  h2 {
    font-size: 28px;
    color: $colour-red;
  }

  .links {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;

    a {
      font-size: 24px;
    }

    .navLink {
      color: white;

      &:hover {
        color: $colour-blue;
      }
    }

    .active {
      color: $colour-orange;
    }
  }
}


@media screen and (max-width: $bp-mobile) {
  .container {
    flex-direction: column;
    justify-content: center;
    gap: 6px;

    h2 {
      font-size: 6vw;
    }

    .links {
      gap: 2vw;

      a {
        font-size: 4vw;
      }
    }
  }
}
